yBAA0B,AAC1B,iBAAmB,CACpB,AACD,2DACE,UAAY,CACb,AACD,wCACE,eAAgB,AAChB,YAAa,AACb,YAAa,AACb,iBAAkB,AAClB,kBAAmB,AACnB,gBAAiB,AACjB,UAAY,CACb","file":"moviesTag.vue","sourcesContent":["/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */\nhtml {\n  color: #666;\n  background: #fff;\n  -webkit-text-size-adjust: 100%;\n  -ms-text-size-adjust: 100%;\n}\n/*鼠标经过 图片放大*/\n.movieImg {\n  cursor: pointer;\n  -webkit-transition: all 0.6s;\n  transition: all 0.6s;\n}\n.movieImg:hover {\n  -webkit-transform: scale(1.4);\n          transform: scale(1.4);\n}\n/* 内外边距通常让各个浏览器样式的表现位置不同 */\nbody,\ndiv,\ndl,\ndt,\ndd,\nul,\nol,\nli,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\npre,\ncode,\nform,\nfieldset,\nlegend,\ninput,\ntextarea,\np,\nblockquote,\nth,\ntd,\nhr,\nbutton,\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  margin: 0;\n  padding: 0;\n}\n/* 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block;\n}\n/* HTML5 媒体文件跟 img 保持一致 */\naudio,\ncanvas,\nvideo {\n  display: inline-block;\n  *display: inline;\n  *zoom: 1;\n}\n/* 要注意表单元素并不继承父级 font 的问题 */\nbody,\nbutton,\ninput,\nselect,\ntextarea {\n  font: 12px/1.62 Helvetica, Arial, sans-serif;\n}\ninput,\nselect,\ntextarea {\n  outline: none;\n  font-size: 100%;\n}\n/* 去掉各Table  cell 的边距并让其边重合 */\ntable {\n  border-spacing: 0;\n  border-collapse: collapse;\n}\n/* IE bug fixed: th 不继承 text-align*/\nth {\n  text-align: inherit;\n  font-weight: bold;\n}\n/* 去除默认边框 */\nfieldset,\nimg {\n  border: 0;\n}\n/* ie6 7 8(q) bug 显示为行内表现 */\niframe {\n  display: block;\n}\n/* 去掉 firefox 下此元素的边框 */\nabbr,\nacronym {\n  border: 0;\n  font-variant: normal;\n}\n/* 一致的 del 样式 */\ndel {\n  text-decoration: line-through;\n}\naddress,\ncaption,\ncite,\ncode,\ndfn,\nem,\nth,\nvar {\n  font-weight: 500;\n  font-style: normal;\n}\n/* 去掉列表前的标识, li 会继承 */\nol,\nul {\n  list-style: none;\n}\n/* 对齐是排版最重要的因素, 别让什么都居中 */\ncaption,\nth {\n  text-align: left;\n}\n/* 来自yahoo, 让标题都自定义, 适应多个系统应用 */\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-size: 100%;\n  font-weight: 500;\n}\nq:before,\nq:after {\n  content: '';\n}\n/* 统一上标和下标 */\nsub,\nsup {\n  position: relative;\n  vertical-align: baseline;\n  font-size: 75%;\n  line-height: 0;\n}\nsup {\n  top: -0.5em;\n}\nsub {\n  bottom: -0.25em;\n}\n/* 正常链接 未访问 */\na:link,\na:visited {\n  color: #08c;\n}\n/*红色的链接*/\na.redlink:link,\na.redlink:visited {\n  color: #910004;\n}\n/* 鼠标悬停 */\na:hover {\n  text-decoration: underline;\n}\n/* 默认不显示下划线，保持页面简洁 */\nins,\na {\n  text-decoration: none;\n}\n/* 代码字体 */\ncode,\nkbd,\npre,\nsamp {\n  font-family: monospace, serif;\n  font-size: 1em;\n}\n/* 清理浮动 */\n.clearfix:after {\n  display: block;\n  visibility: hidden;\n  clear: both;\n  height: 0;\n  content: \" \";\n  font-size: 0;\n}\n.clearfix {\n  zoom: 1;\n  /* for IE6 IE7 */\n}\n/* 隐藏, 通常用来与 JS 配合 */\n.fn-hide {\n  display: none;\n}\n/* 设置内联, 减少浮动带来的bug */\n.fn-left,\n.fn-right {\n  display: inline;\n}\n.fn-left {\n  float: left;\n}\n.fn-right {\n  float: right;\n}\n/* 单行文字溢出时出现省略号，需设定宽度 */\n.fn-text-overflow {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\n/* 简单的渐变 */\n.fn-linear {\n  background: -webkit-gradient(linear, left top, left bottom, from(#fbfbfb), to(#f2f2f2));\n  background: -webkit-linear-gradient(top, #fbfbfb, #f2f2f2);\n  background: linear-gradient(top, #fbfbfb, #f2f2f2);\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fbfbfb', endColorstr='#f2f2f2');\n}\n/* 浅色的渐变 */\n.fn-linear-light {\n  background: -webkit-gradient(linear, left top, left bottom, from(#fdfdfd), to(#eee));\n  background: -webkit-linear-gradient(top, #fdfdfd, #eee);\n  background: linear-gradient(top, #fdfdfd, #eee);\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fdfdfd', endColorstr='#eee');\n}\n/* chrome 下字体过小的问题 */\n.fn-webkit-adjust {\n  -webkit-text-size-adjust: none;\n}\n/* placeholder颜色*/\n::-webkit-input-placeholder {\n  /* WebKit browsers */\n  color: #999;\n}\n:-moz-placeholder {\n  /* Mozilla Firefox 4 to 18 */\n  color: #999;\n}\n::-moz-placeholder {\n  /* Mozilla Firefox 19+ */\n  color: #999;\n}\n:-ms-input-placeholder {\n  /* Internet Explorer 10+ */\n  color: #999;\n}\n/*一些页面宽度*/\n.grid-950 {\n  width: 950px;\n  margin: 0 auto;\n}\n/*.container{\n    padding-top: 10px;\n    background: #f3f3f3;\n    min-width: 950px;\n}*/\n/*动画 fade,bounce*/\n.fade-enter-active,\n.fade-leave-active {\n  -webkit-transition: opacity 0.2s;\n  transition: opacity 0.2s;\n}\n.fade-enter-active,\n.fade-leave-active {\n  opacity: 0;\n}\n.bounce-enter-active {\n  -webkit-animation: bounce-in 0.5s;\n          animation: bounce-in 0.5s;\n}\n.bounce-leave-active {\n  -webkit-animation: bounce-out 0.5s;\n          animation: bounce-out 0.5s;\n}\n@-webkit-keyframes bounce-in {\n0% {\n    -webkit-transform: scale(0);\n            transform: scale(0);\n}\n50% {\n    -webkit-transform: scale(1.5);\n            transform: scale(1.5);\n}\n100% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n}\n}\n@keyframes bounce-in {\n0% {\n    -webkit-transform: scale(0);\n            transform: scale(0);\n}\n50% {\n    -webkit-transform: scale(1.5);\n            transform: scale(1.5);\n}\n100% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n}\n}\n@-webkit-keyframes bounce-out {\n0% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n}\n50% {\n    -webkit-transform: scale(1.5);\n            transform: scale(1.5);\n}\n100% {\n    -webkit-transform: scale(0);\n            transform: scale(0);\n}\n}\n@keyframes bounce-out {\n0% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n}\n50% {\n    -webkit-transform: scale(1.5);\n            transform: scale(1.5);\n}\n100% {\n    -webkit-transform: scale(0);\n            transform: scale(0);\n}\n}\n.moviesList {\n  width: 950px;\n  margin: 10px auto 0;\n  min-height: 500px;\n}\n.moviesList .movie-container {\n  width: 560px;\n  border-top: 1px dashed #ccc;\n  margin-top: -10px;\n}\n.moviesList .movie-container .movieTag {\n  padding-top: 20px;\n  width: 118px;\n  height: 270px;\n  overflow: hidden;\n  font-size: 12px;\n  text-align: center;\n  display: inline-block;\n  margin: 0 20px 20px 0;\n}\n.moviesList .movie-container .movieTag ul {\n  margin: 0 auto;\n  padding: 0;\n}\n.moviesList .movie-container .movieTag ul li {\n  list-style: none;\n}\n.moviesList .movie-container .movieTag a {\n  cursor: pointer;\n  text-decoration: none;\n}\n.moviesList .movie-container .movieTag .film-pic {\n  height: 180px;\n  margin-bottom: 12px;\n  overflow: hidden;\n}\n.moviesList .movie-container .movieTag .film-pic a img {\n  width: 128px;\n  vertical-align: middle;\n}\n.moviesList .movie-container .movieTag .film-name {\n  font-size: 14px;\n  white-space: nowrap;\n  height: 22px;\n  overflow: hidden;\n}\n.moviesList .movie-container .movieTag .film-name a {\n  display: inline-block;\n  word-spacing: normal;\n  height: 24px;\n  line-height: 24px;\n  text-decoration: none;\n  color: black;\n}\n.moviesList .movie-container .movieTag .film-rate {\n  display: inline-block;\n  margin: 4px auto 2px;\n  height: 19px;\n}\n.moviesList .movie-container .movieTag .film-rate .el-rate {\n  display: inline-block;\n}\n.moviesList .movie-container .movieTag .film-rate .el-rate .el-rate__icon {\n  font-size: 12px;\n}\n.moviesList .movie-container .movieTag .film-rate .rateNum {\n  display: inline-block;\n  font-size: 12px;\n}\n.moviesList .movie-container .movieTag .film-button span {\n  display: block;\n  margin: 0 auto;\n  text-align: center;\n  width: 90px;\n  height: 24px;\n  line-height: 24px;\n  background-color: #268dcd;\n  border-radius: 2px;\n}\n.moviesList .movie-container .movieTag .film-button span a {\n  color: #fff;\n}\n.moviesList .movie-container .load-more {\n  cursor: pointer;\n  width: 530px;\n  height: 30px;\n  line-height: 30px;\n  text-align: center;\n  background: #eee;\n  color: #27a;\n}\n"],"sourceRoot":""}]);

